<template>
    <div>
        <common-header title="考场列表" />
        <div class="location_warp">
            <location :locationSuccessCallBack='locationSuccessCallBack' />
            <search placeholder="搜索考场/候选人" v-bind:searchClick="searchClick" v-bind:readonly="true" />
        </div>
        <div class="data_real_warp">
            <div class="mui-scroll">
                <ul v-if='examRoomList.length>0' id="examlist" class="examlist" @touchend="changeNextPageEnd($event)">
                    <examitem v-for="(item,index) in examRoomList" v-bind:key="item.RoomID" :examinfo='item' />
                    <li class="loading" v-if="examRoomList.length>0">
                        <span>{{loadingMsg}}</span>
                    </li>
                </ul>
                <div class="no_data_icon" v-else>
                    <div class="no_data_img">
                    </div>
                    <p>暂无考场</p>
                </div>
            </div>
        </div>
        <common-footer :currenttype='1' />
    </div>
</template>

<script>
import search from './common/SearchInput';
import location from './common/Location';
import examitem from './common/ExaminationItem';
import Header from './common/Header';
import Footer from './common/Footer';
import { mapGetters, mapActions } from "vuex";
export default {
    data() {
        return {
            //考场默认搜索参数
            paramExam: { roomName: null, cityId: null, pageIndex: 1, pageSize: 5 },
            loadingMoreExam: false,
            loadingMsg: '上拉显示更多内容',
        }
    },
    methods: {
        ...mapActions([
            "getExamRoomList",
            "clearExamRoomList",
        ]),
        getAllData() {
            let self = this;
            self.getExamRoomList(self.paramExam).then((data) => {
                if (data.length >= self.paramExam.pageSize) {
                    self.loadingMoreExam = true;
                    self.loadingMsg = '上拉显示更多内容';
                }
                else {
                    self.loadingMoreExam = false;
                    self.loadingMsg = '没有更多数据了';
                }
            });
        },
        locationSuccessCallBack: function (result) {
            let self = this;
            //每次切换城市,清空上一次搜索记录
            self.clearExamRoomList();
            self.paramExam.cityId = result.CityId;
            self.getAllData();

            // if (self.loadCount === 1) {
            //     self.loadCount++;
            //     mui.init({
            //         pullRefresh: {
            //             container: ".data_real_warp",//待刷新区域标识，querySelector能定位的css选择器均可，比如：id、.class等
            //             up: {
            //                 height: 50,//可选.默认50.触发上拉加载拖动距离
            //                 auto: true,//可选,默认false.自动上拉加载一次
            //                 contentrefresh: "正在加载...",//可选，正在加载状态时，上拉加载控件上显示的标题内容
            //                 contentnomore: '没有更多数据了',//可选，请求完毕若没有更多数据时显示的提醒内容；
            //                 callback: function () {
            //                     let muiSelf = this;
            //                     self.getExamRoomList(self.paramExam).then(
            //                         (data) => {
            //                             self.paramExam.pageIndex++;
            //                             if (data && data.length === 5) {
            //                                 muiSelf.endPullupToRefresh(false);
            //                             } else {
            //                                 muiSelf.endPullupToRefresh(true);
            //                             }

            //                             //  muiSelf.endPullupToRefresh(false);
            //                         }
            //                     )
            //                 } //必选，刷新函数，根据具体业务来编写，比如通过ajax从服务器获取新数据；
            //             }
            //         }
            //     });
            // }
            //  mui('.data_real_warp').pullRefresh().pullupLoading();
            // mui('.data_real_warp').pullRefresh().refresh(true);
        },
        searchClick() {
            this.$router.push({ name: 'ExaminationSearch' });
        },
        redirectResumeList(roomID) {
            this.$router.push({ name: 'ResumeList', query: { roomid: roomID } });
        },
        changeNextPageStart(ev) {
        },
        changeNextPageMove(ev) {
        },

        changeNextPageEnd(ev) {
            let self=this;
            const eleScrollTop = window.document.documentElement.scrollTop;
            const eleScrollHeight = window.document.documentElement.scrollHeight;
            const eleClientHeight = window.document.documentElement.clientHeight;
            if (eleScrollTop + eleClientHeight >= eleScrollHeight - 5) {

                if (this.loadingMoreExam) {
                    this.paramExam.pageIndex++;
                }
                else {
                    return;
                }
                self.getAllData();
            }
        },
    },
    computed: {
        ...mapGetters([
            "examRoomList",

        ])
    },
    created() {

    },
    components: {
        search,
        location,
        'common-header': Header,
        examitem,
        'common-footer': Footer
    }
}
</script>
<style lang="less" scoped>
@import (reference) "../assets/css/base.less";

.loading {
  text-align: center;
  background-color: #efeff4;
  font-size: 15/@r;
  line-height: 40/@r;
  color: #777;
}
body {
  background: #f3f6f9;
}
.location_warp {
  margin-top: 2.2rem;
  background: #fff;
  padding: 21/@r 0 21/@r 30/@r;
  box-sizing: border-box;
}
.examlist {
  margin-bottom: 88/@r;
}
.no_data_icon {
  text-align: center;
  .no_data_img {
    background: url("../assets/images/icon_no_data.png") no-repeat;
    width: 350/@r;
    height: 170/@r;
    background-size: contain;
    margin: 0 auto 60/@r auto;
  }
  font-size: 22/@r;
  color: #909da0;
  padding: 160/@r 0;
}
</style>





